.head{
   display: flex;
   flex-direction: column;
   text-align: center;
   position: relative;

}
.range{
    position: relative;
    width: 200px;
    margin: auto;

}
.range .line{
    
    height: 1px;
    background-color: #f85618;
}
.range .radius{
    position: absolute;
    top: -6px;
    left: 100px;
    
    height: 13px;
    width: 13px;
    border-radius: 7px;
    background-color: rgb(246, 137, 34);

}
.button {
    width: 40px;
    height: 40px;
    background-color: #e9e9e9;
    border-radius: 10px;
    margin-left: auto;
    position: absolute;
    bottom: -20px;
    right: 300px;
}
.button img{
    
    width: 30px;
    height: 20px;
}