.headContent{

    background-color: #e9e9e9;
    position: relative;
    padding: 20px;
    border-radius: 15px;
    text-align: center;
    width: 30rem;
    height: 18rem;
}
.imageAnimation{
    position: relative;    
    width: 150px;
    height: 150px;
    top: -200px;
    left: 165px;
    animation-name: example;
    animation-duration: 0.5s;  
    animation-iteration-count: 1;
}
.image {
    position: absolute;
    top: -75px;
    right: 190px;
    width: 150px;
    height: 150px;
    
}
.image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}
.button{
    position: absolute;
    bottom: 10px;
    right: 170px;
}

@keyframes example {
    from {top: -200px;}
    to {left: -100px; top: -100px;}    
}

.para{
    margin-top: 4rem;
    height: 2rem;
   
    
}
.para h1{
    
    letter-spacing: 1px;
}
.imageAnimation img{
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 50%;
}

button{
    cursor: pointer;
    background-color: transparent;
    border: none;
    margin: 0rem 1rem;
}


.num{
    position: absolute;
    background-color: white;
    height: 60px;
    display: flex;
    justify-content: center;
    width: 60px;
    top: -30px;
    right: -30px;
    align-items: center;
    border-radius: 50%;

}
.num .sup{
    height: 45px;
    padding: auto;
    width: 45px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    color: black;
    font-size: 23px;
    font-weight: bold;
    border-color: white;
}
sup{
    color: orange;
    font-size: 25px;
    font-weight: bold;
}
.headContent button img{
    width: 40px;
    height: 40px;
}
.arrow{
    transform: rotate(180deg);
}