.right{
    width: 15rem;
    height: 8.5rem;
    padding: 10px;
    background-color: #e9e9e9;
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 15px;
    text-align: center;
    
}
.right p{
    font-size: 15px;
}
.rightContent{
    margin-top: 2.7rem;
}
.rightImage{
    position: absolute;
    width: 100px;
    height: 100px;
    top: -50px;
    right: 75px;
}
.rightImage img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}
.num{
    position: absolute;
    background-color: white;
    height: 40px;
    display: flex;
    justify-content: center;
    width: 40px;
    top: 0;
    right: -19px;
    align-items: center;
    border-radius: 50%;
}
.validRight{
        width: 15rem;
        height: 8.5rem;
        padding: 10px;
        background-color: transparent;
        position: relative;
        display: flex;
        align-items: center;
        border-radius: 15px;
        text-align: center;
}
.validImage{
    position: absolute;
    width: 100px;
    height: 100px;
    top: 50px;
    left: -75px;
}
.validImage img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}
.num .sup{
    height: 35px;
    padding: auto;
    width: 35px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    color: black;
    font-size: 20px;
    font-weight: bold;
    border-color: white;
}
.sup sup{
    color: orange;
    font-size: 18px;
    font-weight: bold;
}